import schedule from "../../schedule.json"
import {
  isThisISOYear,
  getISOYear,
  getISOWeek,
  isPast,
  isThisWeek,
} from "date-fns"

const thisYearsSchedule = schedule[getISOYear(new Date())]

const isBooked = week => {
  const scheduleWeek = thisYearsSchedule[getISOWeek(week)]
  return scheduleWeek && scheduleWeek.booked
}

const Week = ({ week, selected, onClick }) => {
  let backgroundColor = "#50E59E"
  if (isPast(week) || isBooked(week)) {
    backgroundColor = "#dbdbdb"
  }
  if (isThisWeek(week)) {
    backgroundColor = "#9f3b29"
  }
  const availableToSelect = !(
    isPast(week) ||
    isBooked(week) ||
    isThisWeek(week)
  )
  return (
    <div
      onClick={onClick}
      css={{
        backgroundColor,
        // gridArea: `w-${getISOWeek(week)}`,
        height: selected && availableToSelect ? 56 : 60,
        borderRadius: 6,
        border: selected && availableToSelect ? "2px white solid" : "none",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        cursor: availableToSelect ? "pointer" : "inherit",
        color: isThisWeek(week) ? "white" : "rgba(0,0,0,0.4)",
        fontSize: 20,
      }}>
      {getISOWeek(week)}
    </div>
  )
}

export default Week



