import React from "react"
import Link from "gatsby-link"
import Schedule from "../components/Schedule"

const HireMePage = () => (
  <React.Fragment>
    <header
      css={{
        gridColumn: 2,
        display: "flex",
        alignItems: "center",
      }}>
      <Link to="/" className="button">
        Back
      </Link>
    </header>
    <section css={{ gridColumn: 2, gridRow: 2 }}>
      <p>
        Thanks so much for your interest! I'll give you a quick overview of what
        it looks like to contract with me. I sell my time{" "}
        <strong>by the week</strong>. When you buy a week, you have me full time
        that week, about <strong>40 hours</strong>.
      </p>
      <p>
        One week is <strong>$5,000</strong>, or about $125/hr.
      </p>
      <p>
        Below I have a schedule of my current availability. Just shoot me an
        email if you're interested in booking one or more weeks.
      </p>
    </section>
    <figure
      css={{
        gridRow: 3,
        gridColumn: "1 / span 3",
        display: "flex",
        justifyContent: "center",
      }}>
      <Schedule />
    </figure>
  </React.Fragment>
)

export default HireMePage



