import classNames from "classnames"
import {
  startOfISOYear,
  isThisISOYear,
  addWeeks,
  getMonth,
  setMonth,
  format,
} from "date-fns"

const currentMonth = getMonth(new Date())
const inFuture = month => month >= currentMonth

const Month = ({ month, area, available }) => (
  <div
    className={classNames("month", { available: available && inFuture(month) })}
    css={{
      gridArea: area,
      display: "grid",
      justifyContent: "center",
      borderBottom: "1px solid #dbdbdb",
      color: available && inFuture(month) ? "#103a61" : "#dbdbdb",
    }}>
    {format(setMonth(new Date(), month), "MMMM")}
  </div>
)

export default Month



