import React from "react"
import Week from "./Week"
import Month from "./Month"
import Responsive from "react-responsive"
import { isAvailable } from "./helpers"
import classNames from "classnames"
import { Set } from "immutable"
import { map, chunk, flatMap, chain, times } from "lodash"
import {
  startOfISOYear,
  isThisISOYear,
  getISOWeek,
  addWeeks,
  getMonth,
  setMonth,
  format,
} from "date-fns"

const Mobile = props => <Responsive {...props} maxWidth={1020} />
const Default = props => <Responsive {...props} minWidth={1021} />

const columnCount = 13
let monthAreas = {}
let availableMonths = new Set()

const weeks = []
let currentWeek = startOfISOYear(new Date())

while (isThisISOYear(currentWeek)) {
  weeks.push(currentWeek)
  if (isAvailable(currentWeek)) {
    availableMonths = availableMonths.add(getMonth(currentWeek))
  }
  currentWeek = addWeeks(currentWeek, 1)
}

const gridTemplateAreas = chain(weeks)
  .chunk(columnCount)
  .flatMap((weekRow, i) => [
    weekRow
      .map(week => {
        const monthArea = `m-${getMonth(week)}-${i}`
        monthAreas[monthArea] = getMonth(week)
        return monthArea
      })
      .join(" "),
    weekRow.map(week => `w-${getISOWeek(week)}`).join(" "),
  ])
  .value()
  .map(row => `"${row}"`)
  .join("\n")

class Schedule extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      selectedWeeks: new Set(),
    }
  }

  render() {
    return (
      <React.Fragment>
        <Default>
          <div
            css={{
              display: "grid",
              gridTemplateColumns: `repeat(${columnCount}, 60px)`,
              gridTemplateAreas,
              gridGap: "25px 20px",
            }}>
            {map(monthAreas, (month, area) => (
              <Month
                month={month}
                area={area}
                available={availableMonths.has(month)}
              />
            ))}
            {weeks.map((week, i) => (
              <Week
                key={i}
                week={week}
                onClick={this.handleWeekClick(week)}
                selected={this.state.selectedWeeks.includes(week)}
              />
            ))}
          </div>
        </Default>
        <Mobile>
          <div
            css={{
              display: "grid",
              gridTemplateColumns: "1fr",
              width: "100%",
              gridGap: "25px 20px",
            }}>
            {times(12).map(month => (
              <React.Fragment key={month}>
                <Month month={month} available={availableMonths.has(month)} />
                <div
                  className="weeks"
                  css={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, 60px)",
                    gridGap: 10,
                    justifyContent: "center",
                  }}>
                  {weeks
                    .filter(week => getMonth(week) == month)
                    .map((week, i) => (
                      <Week
                        key={i}
                        week={week}
                        onClick={this.handleWeekClick(week)}
                        selected={this.state.selectedWeeks.includes(week)}
                      />
                    ))}
                </div>
              </React.Fragment>
            ))}
          </div>
        </Mobile>
      </React.Fragment>
    )
  }

  handleWeekClick(week) {
    return e => {
      if (this.state.selectedWeeks.includes(week)) {
        this.setState({ selectedWeeks: this.state.selectedWeeks.delete(week) })
      } else {
        this.setState({ selectedWeeks: this.state.selectedWeeks.add(week) })
      }
    }
  }
}

export default Schedule



